import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as AppActions from './../../actions/app';
import * as GameActions from './../../actions/game';
import * as AuthActions from './../../actions/auth';
import Home from './../../components/Home';
import Intro from './../../components/HomeIntro';
import Genres from './../../components/HomeGenres';
import Instructions from './../../components/Instructions';
import { GENRES_LIST } from './../../core/constants';

class HomeContainer extends Component {
  constructor() {
    super();
    this._selectGenre = this._selectGenre.bind(this);
  }

  componentDidMount() {
    if (location.hash.split('access_token=')[1]) {
      let token = location.hash.split('access_token=')[1].split('&')[0];
      let expires = location.hash.split('&expires_in=')[1].split('?')[0];
      this.props.authActions.fbContinueLogin(token, expires);
    } else {
      this.props.appActions.reset();
      this.props.gameActions.reset();
    }
  }

  render() {
    const { genres, searchingLabel, instructions, lastSelectedGenre } = this.props.app;
    const lastGenreIndex = lastSelectedGenre ? Object.keys(GENRES_LIST).indexOf(lastSelectedGenre) : 0;
    const genreList = [];
    Object.keys(genres).map(key => {
      genreList.push(genres[key]);
    });

    return (
      <Home loadingLabel={searchingLabel}>
		{
          instructions ? <Instructions onHide={this.props.appActions.hideInstructions}/> : ''
        }
        <Intro />
        <Genres list={genreList} current={lastGenreIndex} onSelect={this._selectGenre}/>
      </Home>
    );
  }

  _selectGenre(index) {
    this.props.appActions.navigateToGame(index);
  }
}

function mapStateToProps(state) {
  return {
    app: state.app
  };
}

function mapDispatchToProps(dispatch) {
  return {
    appActions: bindActionCreators(AppActions, dispatch),
    gameActions: bindActionCreators(GameActions, dispatch),
    authActions: bindActionCreators(AuthActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(HomeContainer);



