import React from 'react';
import ReactDOM from 'react-dom';

import Routes from './Routes';
import store from './store/store';

import Fusenlabs from './core/Fusenlabs';

const myStore = store();

let Fusen = Fusenlabs.instance;
if (myStore.getState().auth.flToken) {
  Fusen.setCredentials(myStore.getState().auth.flToken, myStore.getState().auth.flExpires, myStore.getState().auth.flExpirationDate);
}

ReactDOM.render(
  <Routes store={myStore} />
  , document.getElementById('app'));



