import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as AppActions from './../../actions/app';
import * as GameActions from './../../actions/game';
import Trivia from './../../components/Trivia';
import Timer from './../../components/TriviaTimer';
import Helper from './../../components/TriviaHelper';
import Options from './../../components/TriviaOptions';
import Player from './../../components/Player';
import TriviaPopup from './../../components/Popup';
import Loading from './../../components/Loading';

class TriviaContainer extends Component {
  constructor() {
    super();
    this._selectOption = this._selectOption.bind(this);
    this._onTimerTick = this._onTimerTick.bind(this);
    this._playerCallbacks = this._playerCallbacks.bind(this);
    this._startRound = this._startRound.bind(this);
    this._interctionTimeout = this._interctionTimeout.bind(this);
    this._resetTimer;
    this._isMount = true;
  }

  componentWillMount() {
    this.props.appActions.reset();
    this.props.gameActions.reset();
    this.props.appActions.selectGenre(this.props.params.genreId);
  }

  componentWillUnmount() {
    this._isMount = false;
  }

  render() {
    const { showWin, isSearching, searchingLabel } = this.props.app;
    const { totalPts } = this.props.game;
    const { options, applyHelp, time, currentTrack } = this.props.game.currentTrivia;
    const showPopUp = showWin;

    return (
      isSearching ? <Loading text={searchingLabel}/> :
      <Trivia>
        <Player
          src={currentTrack.preview_url}
          timeUpdate={this._onTimerTick}
          methods={this._playerCallbacks}
        />
        <Timer time={time} />
        <Helper help={applyHelp} track={currentTrack} />
        <Options list={options} onSelect={this._selectOption} removeOptions={applyHelp.removeOptions} />
        <TriviaPopup
          show={showPopUp}
          onClick={this._startRound}
          score={totalPts}
        />
      </Trivia>
    );
  }

  _playerCallbacks(methods, resetCallback) {
    // methods return full <audio> api, this is an example
    this.setState({ audio: methods });
    this._resetTimer = resetCallback;
  }

  _selectOption(optIndex) {
    const { options, currentTrack } = this.props.game.currentTrivia;
    const next = options[optIndex].id === currentTrack.id
      ? this.props.gameActions.win
      : this.props.gameActions.lose;

    next();
    this._resetTimer();
    this.state.audio.pause();
  }

  _onTimerTick(time) {
    if (this._isMount) {
      if (time !== 0) {
        this.props.gameActions.timerTick(time);
      } else {
        this.props.gameActions.lose();
      }
    }
  }

  _startRound() {
    this.state.audio.play();
    this.props.appActions.hidePopup();
  }

  _interctionTimeout() {
    // lost for user inactivity
    this.props.gameActions.lose();
  }
}

function mapStateToProps(state) {
  return {
    app: state.app,
    game: state.game
  };
}

function mapDispatchToProps(dispatch) {
  return {
    appActions: bindActionCreators(AppActions, dispatch),
    gameActions: bindActionCreators(GameActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(TriviaContainer);



