import React, { Component } from 'react';
import ReactSwipe from 'react-swipe';
// import { GENRES_LIST } from '../../core/constants';

// require('./_index.scss');

class Genres extends Component {
  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.prev = this.prev.bind(this);
  }
  render() {
    const { list, current, onSelect } = this.props;
    const genres = list.map((item, index)=> {
      let divStyleGenre = {
        backgroundImage: `url(./images/infinite-music-trivia_characters_${item.name.toLowerCase()}.png)`
      };
      let divStyleStars = {
        backgroundImage: `url(./images/infinite-music-trivia_characters_${item.name.toLowerCase()}_stars.png)`
      };
      return (
        <li key={index}>
          <div className="slide">
            <div className="character" style={divStyleGenre}></div>
            <a className="stars" style={divStyleStars} href={`./#/game/${item.id}`}></a>
          </div>
        </li>
      );
    });

    return (
      <div className="Genres">
        <button className="slide-control left" onClick={this.prev}></button>
        <button className="slide-control right" onClick={this.next}></button>
        <ul>
          <div className="wrapper">
          <div className="fix">

            <ReactSwipe
              ref="ReactSwipe"
              continuous
              startSlide={current}
            >
              {genres}
            </ReactSwipe>
          </div>
          </div>
        </ul>
      </div>
    );
  }

  next() {
    this.refs.ReactSwipe.swipe.next();
  }

  prev() {
    this.refs.ReactSwipe.swipe.prev();
  }
}

export default Genres;



