import Facebook from './../core/Facebook';
import Fusenlabs from './../core/Fusenlabs';
import * as appActions from './app';
import { hashHistory as history } from 'react-router';

let FB = new Facebook();
let Fusen = Fusenlabs.instance;
let save_score = null;
let share_callback = null;
let goToLeaderboard = null;

const setFbToken = (token, expires) => {
  return {
    type: 'AUTH_SET_FB_CREDENTIALS',
    token,
    expires
  };
};

const setFlToken = (token, expires, flExpirationDate) => {
  Fusen.setCredentials(token, expires, flExpirationDate);
  return {
    type: 'AUTH_SET_FL_CREDENTIALS',
    token,
    expires,
    flExpirationDate
  };
};

export function sendFlFriends() {
  return dispatch => {
    FB.getFriends().then(response => {
      let friends = response.data.map(item => {
        return item.id;
      });
      Fusen.saveFriends(friends).then(() => {
        if (goToLeaderboard) {
          history.replace(`/leaderboard/${goToLeaderboard}`);
          dispatch(appActions.stopSearching());
        }
      });
    });
  };
}

export function fbLogin() {
  return dispatch => {
    FB.login().then(fbResponse => {
      dispatch(
        setFbToken(
          fbResponse.authResponse.accessToken,
          fbResponse.authResponse.expiresIn
        )
      );
      dispatch(
        flLogin(
          fbResponse.authResponse.accessToken,
          fbResponse.authResponse.expiresIn
        )
      );
    }).catch(() => {
      dispatch(appActions.stopSearching());
    });
  };
}

export function fbContinueLogin(token, expires) {
  return (dispatch, store) => {
    save_score = {
      score: store().game.totalPts,
      genre: store().app.selectedGenre
    };
    history.push('/finish');
    dispatch(
      setFbToken(
        token,
        expires
      )
    );
    dispatch(
      flLogin(
        token,
        expires
      )
    );
  };
}

export function flLogin(token, exipres) {
  return dispatch => {
    Fusen.login(token, exipres).then(FlResponse => {
      let expirationDate = Math.floor(Date.now() / 1000) + FlResponse.accessTokenExpiration;
      dispatch(setFlToken(FlResponse.accessToken, FlResponse.accessTokenExpiration, expirationDate));
      dispatch(sendFlFriends());
      if (!goToLeaderboard) {
        dispatch(appActions.stopSearching());
      }
      if (save_score) {
        dispatch(savaScore(save_score.genre, save_score.score));
      }
      if (share_callback) {
        dispatch(share(save_score.score, save_score.genre));
      }
    });
  };
}

export function login(score_save, share, goToLead) {
  ga('send', 'event', 'event', 'fblogin', 'true');
  save_score = score_save;
  share_callback = share;
  goToLeaderboard = goToLead;
  return (dispatch) => {
    dispatch(appActions.startSearching('getting tickets'));
    dispatch(fbLogin());
  };
}

export function savaScore(genre, score) {
  return (dispatch) => {
    Fusen.saveScore(genre, score);
  };
}

export function share(genre, score) {
  ga('send', 'event', 'event', 'share', 'true');
  return (dispatch) => {
    dispatch(appActions.startSearching('showing off your talent'));
    FB.postScore(genre, score).then(() => {
      dispatch(appActions.stopSearching());
    });
  };
}

export function apiStatus(response) {
  return dispatch => {
    if (response.status === 401) {
      dispatch(login());
    }
  };
};



