/*
 * to restore timer
 * <Timer time={this.state.timeout} style={{ zIndex: 120 }} />
 */

import React, { Component } from 'react';
import WellDone from './../WellDone';
import ReadyToPlay from './../ReadyToPlay';
import Timer from './../../components/TriviaTimer';

// require('./_index.scss');

class Popup extends Component {

  componentWillMount() {
    this.setState({ timeout: this.props.timeout });
  }

  componentWillUnmount() {
    clearInterval(this.clearTimer);
  }

  componentDidMount() {
    // initiates timer the first time.
    this._timer();
  }

  componentWillUpdate(nextProps) {
    if (this.props.show !== nextProps.show) {
      if (this.props.show) {
        // reset timer on popup hide
        this._reset();
      } else {
        // initiates timer on popup show
        this._timer();
      }
    }
  }

  render() {
    const { show, score, onClick } = this.props;
    const className = 'Popup' + (show ? ' in' : '');
    const popupContent = score === 0
        ? <ReadyToPlay onClick={onClick}/>
        : <WellDone onClick={onClick} score={score}/>;
    return (
      <div className={className}>
        {popupContent}
      </div>
    );
  }

  _timer() {
    this.clearTimer = setInterval(() => {
      this.setState({ timeout: --this.state.timeout });
      if (this.state.timeout === 0) {
        clearInterval(this.clearTimer);
        this.props.onTimeout();
      }
    }, 1000);
  }

  _reset() {
    this.setState({ timeout: this.props.timeout });
    clearInterval(this.clearTimer);
  }
}

Popup.defaultProps = {
  timeout: 5,
  onTimeout: f=> f,
  onClick: f=> f
};

export default Popup;



