const initialState = {
  friends: []
};

export default (state = initialState, action) => {
  const switchObj = {
    LEADERBOARD_SET_FRIENDS: ()=> Object.assign({},
      state,
      { friends: action.friends }
    ),
    DEFAULT: ()=> state
  };
  return (switchObj.hasOwnProperty(action.type) && switchObj[action.type] || switchObj.DEFAULT)();
};



