/*
 * Music
 *
 * example:
 *
 * var game = new Music();
 * game.category('ROCK');
 * game.getBulk().then(tracks => {});
 */
import {Client, TrackHandler, ArtistHandler, UserHandler} from 'spotify-sdk';
import { COUNTRY, GENRES_LIST } from './constants';
import helpers from './helpers';

export default class Music {
  constructor() {
    this.client = Client.instance;
    this.track = new TrackHandler();
    this.artists = new ArtistHandler();

    this._category = '';
    this._nextBatch = [];
  }

  category(cat) {
    this._category = cat;
  }

  getTracks(q) {
    return this.track.search(q, { limit: 50, market: COUNTRY });
  }

  getTracksList(list) {
    return new Promise(resolve => {
      let createGroupedArray = (arr, chunkSize) => {
        let groups = [], i;
        for (i = 0; i < arr.length; i += chunkSize) {
          groups.push(arr.slice(i, i + chunkSize));
        }
        return groups;
      };
      let newList = createGroupedArray(list, 50);
      let promises = newList.map(list => {
        return this.track.get(list).then(trackCollection => {
          return trackCollection.map(track => {
            return track;
          });
        });
      });
      Promise.all(promises).then(response => {
        resolve(response);
      });
    });
  }

  paginate(collection, page) {
    return new Promise((resolve) => {
      let currentPage = 1;
      let promises = [];

      let next = (nextCollection) => {
        if (currentPage <= page - 1 && nextCollection._next !== null) {
          currentPage++;
          let prom = nextCollection.next.then(resp => {
            next(resp);
            return resp.map(track => {
              return track;
            });
          });
          promises.push(prom);
          return prom;
        } else {
          return Promise.all(promises).then(response => {
            resolve(response);
          });
        }
      };

      promises.push(collection.then(response => {
        return next(response);
      }));

    });
  }

  fromRelatedArtist(artists) {
    return new Promise((resolve) => {
      let promises = [];
      let finish = () => {
        Promise.all(promises).then(response => {
          resolve([].concat.apply([], response));
        });
      };
      return artists.map((id, i) => {
        return this.artists.get([id]).then(artist => {
          return artist[0].relatedArtists().then(artistsCollection => {
            artistsCollection.length = 9;
            artistsCollection.push(artist[0]);
            artistsCollection.map((currentArtist, e) => {
              promises.push(currentArtist.topTracks({ country: COUNTRY }).then(trackCollection => {
                trackCollection.length = 10;
                if (i === artists.length - 1 && artistsCollection.length - 1 === e) {
                  finish();
                }
                return trackCollection.map(track => {
                  return track;
                });
              }));
            });
          });
        });
      });
    });
  }

  magic(tracks) {
    return tracks.then(response => {
      let songs = helpers.beatufulNames(
                    helpers
                    .orderByPopularity([].concat.apply([], helpers.removeDuplicates(response)))
                    .splice(0, 100)
                  );
      let shuffledSongs = helpers.shuffle(songs);
      let current = shuffledSongs.splice(0, 50);
      this._nextBatch = shuffledSongs;
      return helpers.alternate(helpers.shuffle(current));
    });
  }

  getBulk() {
    if (this._nextBatch.length) {
      return new Promise((resolve) => {
        resolve(this._nextBatch);
      });
    } else {
      let data = GENRES_LIST[this._category];
      if (data.type === 'by_artist') {
        return this.magic(this.fromRelatedArtist(data.list));
      } else if (data.type === 'by_query') {
        return this.magic(this.paginate(this.getTracks(data.data), 10));
      } else if (data.type === 'custom') {
        return this.magic(this.getTracksList(data.list));
      }
    }
  }
}



