import { GENRES_LIST } from './../core/constants';

const initialState = {
  selectedGenre: '',
  lastSelectedGenre: '',
  genres: GENRES_LIST,
  isSearching: true,
  searchingLabel: '',
  showWin: true,
  rounds: 0,
  instructions: localStorage['fl_instructions'] === 'false' ? false : true
};

export default (state = initialState, action) => {
  const switchObj = {
    SELECT_GENRE: ()=> Object.assign({},
      state,
      { selectedGenre: action.genre, lastSelectedGenre: action.genre }
    ),
    LOADING: ()=> Object.assign({},
      state,
      { isSearching: action.value, searchingLabel: action.text }
    ),
    SWITCH_WIN: ()=> Object.assign({},
      state,
      { showWin: action.value }
    ),
    NEW_ROUND: ()=> Object.assign({},
      state,
      { rounds: state.rounds++ }
    ),
    SWITCH_INSTRUCTIONS: ()=> Object.assign({},
      state,
      { instructions: action.value }
    ),
    RESET_APP: () => Object.assign({}, initialState, {
      lastSelectedGenre: state.lastSelectedGenre,
      instructions: state.instructions
    }),
    DEFAULT: ()=> state
  };
  return (switchObj.hasOwnProperty(action.type) && switchObj[action.type] || switchObj.DEFAULT)();
};



