import { hashHistory as history } from 'react-router';
import Fusenlabs from './../core/Fusenlabs';
import * as appActions from './app';

let Fusen = Fusenlabs.instance;

export function setFriends(friends) {
  return {
    type: 'LEADERBOARD_SET_FRIENDS',
    friends
  };
};

export function getFriends(genreIndex) {
  return (dispatch) => {
    dispatch(appActions.startSearching('gathering the band'));
    Fusen.getLeaderboard(genreIndex)
    .then(response => {
      dispatch(setFriends(response.leaderboard));
      dispatch(appActions.stopSearching());
    })
    .catch(response => {
      history.replace('/');
      dispatch(appActions.stopSearching());
    });
  };
};



