import React, { Component } from 'react';
import { Provider } from 'react-redux';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Router, Route, IndexRoute, hashHistory } from 'react-router';
import * as AppActions from './actions/app';

import Home from './containers/Home';
import Trivia from './containers/Trivia';
import Incorrect from './containers/Incorrect';
import Leaderboard from './containers/Leaderboard';

const getOnEnterFinish = (store)=> {
  return (nextState, redirect) => {
    ga('send', 'screenview', {
      'screenName': 'Finish'
    });
    const gameStore = store.getState().game;
    if (!gameStore.currentTrivia.currentTrack.original) {
      redirect('/');
    }
  };
};

const getOnEnterGame = (store)=> {
  return (nextState, redirect) => {
    ga('send', 'screenview', {
      'screenName': 'Game'
    });
    const appStore = store.getState().app;
    if (!appStore.genres[nextState.params.genreId]) {
      redirect('/');
    }
  };
};

const getOnEnterLeaderboard = (store) => {
  return (nextState, redirect) => {
    ga('send', 'screenview', {
      'screenName': 'Leaderboard'
    });
    const authStore = store.getState().auth;
    const integrityFail = authStore.flToken !== null && authStore.flExpirationDate > Math.floor(Date.now() / 1000);
    if (!integrityFail) {
      redirect('/');
    }
  };
};

const getOnEnterHome = () => {
  return (nextState, redirect) => {
    ga('send', 'screenview', {
      'screenName': 'Home'
    });
  };
};

const Routes = ({ store }) => (
  <Provider store={store}>
    <Router history={hashHistory}>
      <Route path="/">
        <IndexRoute component={Home} onEnter={getOnEnterHome()}/>
        <Route path="game/:genreId" component={Trivia} onEnter={getOnEnterGame(store)}/>
        <Route path="finish" component={Incorrect} onEnter={getOnEnterFinish(store)}/>
        <Route path="/leaderboard/:genderId" component={Leaderboard} onEnter={getOnEnterLeaderboard(store)}/>
      </Route>
      <Route path="*" component={Home}/>
    </Router>
  </Provider>
);


export default Routes;



