import store from './../store/store';
import * as gameActions from './../actions/game';
import * as appActions from './../actions/app';
import MusicSource from './Music';
import helpers from './helpers';

class Game {
  /**
  * Performs a search with current selected genre.
  * Uses Music provider to get results.
  * @param {Object} appState A plain object representing the app store.
  * @return Promise
  */
  static start(appState) {
    this.source = new MusicSource();
    this.source.category(appState.selectedGenre);
    return this.source.getBulk();
  }

  static loadMoreTracks() {
    return this.source.getBulk();
  }

  /**
  * Handles process to each play cicle.
  */
  static play(state) {
    const tracks = [].slice.call(state.totalTracks);
    const playedTracks = [].slice.call(state.playedTracks);
    // extract a random track from a copy of state.totalTracks
    const currentTrack = tracks.splice(~~(Math.random() * tracks.length), 1)[0];

    // choose other 3 as options.
    const getOpts = (size = 1, source = [])=> {
      let options = [];
      while (size--) {
        options.push(source.splice(~~(Math.random() * source.length), 1)[0]);
      }
      return options;
    };

    // aditional options can be any of played tracks or unplayed tracks, except current track.
    let options = getOpts(3, [].slice.call(tracks.concat(playedTracks)));

    // set 2 options to be removed on help level 4
    // when played tracks are higher than 50, make it a little more difficult and hide just 1 option
    // all options will be shuffled later
    options[0].removable = true;
    options[1].removable = true && playedTracks.length < 49;
    options[2].removable = false;
    currentTrack.removable = false;

    // add currentTrack to options list
    options.push(currentTrack);

    // shuffles options (including currentTrack) to set into state.currentTrivia.options.
    options = helpers.shuffle(options);

    // formats extracted track to be setted as state.currentTrivia.currentTrack
    const track = {
      id: currentTrack.id,
      // not sure of including original track here, I think could be inspected somehow and use it to cheat.
      // but this track could be chosen later as option and then I'll need the name to show.
      // @TODO review this situation.
      original: currentTrack, // object to use when stores played tracks.

      preview_url: currentTrack.preview_url,
      album_cover: currentTrack.album.images[0].url,
      album_name: currentTrack.album.name,
      release_year: 1999,
      artist_name: currentTrack.artists[0].name
    };

    return { track, options, tracksLeft: tracks };
  }

  static evaluateHelpLevel(state) {
    const helpLvls = 5;
    const maxTime = 30;
    const time = state.currentTrivia.time;
    const lvl = 'LVL' + ((helpLvls - ~~((time * 100 / maxTime) * helpLvls / 100)) - 1);

    return lvl;
  }

  static getScore(state) {
    return ~~state.currentTrivia.time;
  }

  static success() {
    this.time2Score();
    this.storePlayedTrack();
    // what else?
  }

  static fail() {
    this.time2Score();
    // what else?
  }

}

Game.source = null;

export default Game;



