import React, { Component } from 'react';

const Instructions = ({onHide}) => (
  <div className="Instructions">
    <div className="intro-fix">
      <div className="container">
        <img src="./images/infinite-music-trivia_instructions_image.png"/>
        Scroll or swipe to <strong>choose a music genre, listen to a tune and guess the song name.</strong><br/>
        The faster you do it, the higher number of points you win.
        <span className="cta-play" onClick={() => {onHide()}}>Got it, let's do this!</span>
      </div>
    </div>
  </div>
);

export default Instructions;



