
const initialState = {
  currentTrivia: {
    currentTrack: {
      id: '',
      preview_url: '',
      album_cover: '',
      album_name: '',
      release_year: '',
      artist_name: '',
      original: null
    },
    helpLvl: 'LVL0', // LVL0: no help
    applyHelp: {
      showCover: false,
      showAlbumName: false,
      showArtistName: false,
      removeOptions: false
    },
    options: [],
    time: 30
  },
  totalTracks: [],
  playedTracks: [],
  totalPts: 0
};

export default (state = initialState, action)=> {
  const switchObj = {

    SET_TRACKS: ({ tracks })=> Object.assign({},
      state,
      { totalTracks: tracks }
    ),

    SET_OPTIONS: ({ options })=> {
      const currentTrivia = Object.assign({}, state.currentTrivia, { options: options });
      return Object.assign({},
        state,
        { currentTrivia }
      );
    },

    SET_CURRENT_TRACK: ({ track })=> {
      const currentTrivia = Object.assign({}, state.currentTrivia, { currentTrack: track });
      return Object.assign({},
        state,
        { currentTrivia }
      );
    },

    UPDATE_PLAYED_TRACKS: ({ track })=> {
      const playedTracks = [].slice.call(state.playedTracks).concat(track);
      return Object.assign({},
        state,
        { playedTracks }
      );
    },

    RESET_HELP: ()=> {
      const currentTrivia = Object.assign({}, state.currentTrivia, { applyHelp: {
        showCover: false,
        showAlbumName: false,
        showArtistName: false,
        removeOptions: false
      }});
      currentTrivia.helpLvl = initialState.currentTrivia.helpLvl;
      return Object.assign({},
        state,
        { currentTrivia }
      );
    },

    RESET_GAME: ()=> Object.assign({}, initialState),

    HELP_LVL1: ({ lvl })=> {
      const currentTrivia = Object.assign({}, state.currentTrivia);
      currentTrivia.applyHelp.showArtistName = true;
      currentTrivia.helpLvl = lvl;
      return Object.assign({},
        state,
        { currentTrivia }
      );
    },

    HELP_LVL2: ({ lvl })=> {
      const currentTrivia = Object.assign({}, state.currentTrivia);
      currentTrivia.applyHelp.showCover = true;
      currentTrivia.helpLvl = lvl;
      return Object.assign({},
        state,
        { currentTrivia }
      );
    },

    HELP_LVL3: ({ lvl })=> {
      const currentTrivia = Object.assign({}, state.currentTrivia);
      currentTrivia.applyHelp.showAlbumName = true;
      currentTrivia.helpLvl = lvl;
      return Object.assign({},
        state,
        { currentTrivia }
      );
    },

    HELP_LVL4: ({ lvl })=> {
      const currentTrivia = Object.assign({}, state.currentTrivia);
      currentTrivia.applyHelp.removeOptions = true;
      currentTrivia.helpLvl = lvl;
      return Object.assign({},
        state,
        { currentTrivia }
      );
    },

    UPDATE_TIME: ({ time })=> {
      const currentTrivia = Object.assign({}, state.currentTrivia, { time });
      return Object.assign({},
        state,
        { currentTrivia }
      );
    },

    UPDATE_SCORE: ({ pts })=> Object.assign({},
      state,
      { totalPts: state.totalPts + pts }
    ),

    DEFAULT: ()=> state
  };

  return (switchObj.hasOwnProperty(action.type) && switchObj[action.type] || switchObj.DEFAULT)(action);
};



