import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import * as LeaderboardActions from './../../actions/leaderboard';
import Loading from './../../components/Loading';
import LeaderboardComp from './../../components/Leaderboard';


class Leaderboard extends Component {
  constructor() {
    super();
    this.state = {
      genre: ''
    };
  }

  componentDidMount() {
    if (this.props.genres[this.props.params.genderId]) {
      this.setState({
        genre: this.props.genres[this.props.params.genderId].name
      });
      this.props.leaderboardActions.getFriends(this.props.params.genderId);
    }
  }

  render() {
    let { leaderboard, isSearching, searchingLabel } = this.props;
    return (
      isSearching ? <Loading text={searchingLabel}/> : <LeaderboardComp friends={leaderboard.friends} genre={this.state.genre}/>
    );
  }
}
function mapStateToProps(state) {
  return {
    genres: state.app.genres,
    isSearching: state.app.isSearching,
    searchingLabel: state.app.searchingLabel,
    leaderboard: state.leaderboard
  };
}

function mapDispatchToProps(dispatch) {
  return {
    leaderboardActions: bindActionCreators(LeaderboardActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Leaderboard);



