let singleton = Symbol();
let singletonEnforcer = Symbol();

class Fusenlabs {
  constructor(enforcer) {
    this.accessToken = '';
    this.expiresIn = '';
    this.userID = '';
    this.base_url = 'https://fusenlabs.co/api/imt';

    if (enforcer != singletonEnforcer) {
      throw "Cannot construct singleton";
    }
  }

  static get instance() {
    if (!this[singleton]) {
      this[singleton] = new Fusenlabs(singletonEnforcer);
    }
    return this[singleton];
  }

  setCredentials(token, expires, expirationDate) {
    this.accessToken = token;
    this.expiresIn = expires;
    localStorage.fl_token = token;
    localStorage.fl_expires = expires;
    localStorage.fl_expirationDate = expirationDate;
  }

  login(fbToken, expires) {
    return fetch(`${this.base_url}/login/facebook`, {
      method: 'POST',
      headers: {
        'Content-type': 'application/json'
      },
      body: JSON.stringify({
        accessToken: fbToken,
        expiresIn: expires
      })
    }).then(response => {
      return response.json();
    });
  }

  logout() {

  }

  saveFriends(friends) {
    return this.request(`${this.base_url}/api/friends`, 'POST', { ids: friends.join(',') });
  }

  saveScore(genre, score) {
    return this.request(`${this.base_url}/api/score/${genre}`, 'POST', { points: score });
  }

  getLeaderboard(genre) {
    return this.request(`${this.base_url}/api/score/${genre}`, 'GET');
  }

  request(url, method, data) {
    let req = {
      method: method,
      headers: {
        'Content-type': 'application/json',
        Authorization: `Bearer ${this.accessToken}`
      }
    };

    if (data) {
      req.body = JSON.stringify(data);
    }

    return fetch(url, req).then(response => {
      if (response.status !== 401) {
        return response.json();
      }
      return response;
    });
  }
}

export default Fusenlabs;



