import React, { Component } from 'react';

// require('./_index.scss');

/*
  'Rock on!'
  'You took our breathe away!'
  'You’re a MANIAC!'
  'You’re only making it right'

  'Hit me baby (one more time)'
  'Play it again, Sam'
  'Shake me all night long!'
  'One more time, just do it again!'
*/

const WellDone = ({ score, onClick }) => (
  <div className="WellDone wrapper-container" onClick={onClick}>

    <div className="welldone-fix">
      <div className="image"></div>
      <h1 className="text-wrapper">Rock on!</h1>
      <div className="score">
        {score > 0 ? '' : ''}Your current score is <span className="figure">{score} pts.</span>
      </div>
      <button className="play-again-cta">Give me the next one! &rsaquo;</button>
    </div>
  </div>
);

export default WellDone;



