import React, { Component } from 'react';

// require('./_index.scss');

const Options = ({ list, onSelect, removeOptions }) => {
  // const getDisplayName = opt => `${opt.name} - ${opt.id}`;
  const getDisplayName = opt => `${opt.name}`;
  const getClass4Help = opt => removeOptions && opt.removable ? 'disabled' : '';
  const optClick = (evt)=> {
    evt.preventDefault();
    const optIndex = evt.target.getAttribute('data-index') || null;
    if (optIndex !== null) {
      onSelect(optIndex);
    }
  };
  const doNothing = evt=> evt.preventDefault();
  return (
    <div className="TriviaOptions">
      <ul>
        {list.map((opt, index)=> (
          <li
            key={opt.id}
            className={getClass4Help(opt)}
          >
            <a href="#"
              data-index={index}
              onClick={!removeOptions || !opt.removable ? optClick : doNothing }
            >
              {getDisplayName(opt)}
            </a>
          </li>)
        )}
      </ul>
    </div>
  );
};

export default Options;



