import React, { Component } from 'react';

// require('./_index.scss');

const Helper = ({ track, help }) => {
  const getMotivational = (<div className="default-helper"><span className="content">name the song</span></div>);
  const getCover = (
    <div className="album-helper">
      <img src={track.album_cover} className="album-cover" />
      <span className="title">From the Album</span>
      <span className="content">{track.album_name}</span>
    </div>
  );
  const getArtistName = (
    <div className="artist-helper">
      <span className="title">By the artist</span>
      <span className="content">{track.artist_name}</span>
    </div>
  );
  const getTimeOutPhrase = (
    <div className="last-helper">
      <span className="content">TIME OUT, PLACE YOUR BET!</span>
    </div>
  );
  const noHelp = !help.showCover && !help.showAlbumName &&
    !help.showArtistName && !help.removeOptions;

  // const getAlbumName = (<div>{track.album_name}</div>);

  const helper = help.removeOptions && getTimeOutPhrase ||
    // help.showAlbumName && getAlbumName ||
    help.showCover && getCover ||
    help.showArtistName && getArtistName ||
    noHelp && getMotivational;

  return (
    <div className="Helper">
      <div className="fix-outter">
        {helper}
      </div>
    </div>
  );
};

export default Helper;



