import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as GameActions from './../../actions/game';
import Results from './../../components/FinalResults';
import * as AuthActions from './../../actions/auth';
import Loading from './../../components/Loading';

class Incorrect extends Component {
  constructor() {
    super();
    this._continue = this._continue.bind(this);
  }

  componentDidMount() {
    if (this._isLogued()) {
      this.props.authActions.savaScore(this.props.genre, this.props.score);
    }
  }

  render() {
    return (
      this.props.app.isSearching ?
      <Loading text={this.props.app.searchingLabel}/>
      :
      <Results
        score={this.props.score}
        songName={this.props.songName}
        onContinue={this._continue}
        onLeaderboard={this._leaderboard}
        onShare={this.props.authActions.share}
        onLogin={this.props.authActions.login}
        genre={this.props.genre}
        loginStatus={this._isLogued()}
      />
    );
  }

  _isLogued() {
    return this.props.auth.flToken !== null && this.props.auth.flExpirationDate > Math.floor(Date.now() / 1000);
  }

  _continue() {
    this.props.gameActions.restart();
  }
}

function mapStateToProps(state) {
  return {
    score: state.game.totalPts,
    songName: state.game.currentTrivia.currentTrack.original.name,
    auth: state.auth,
    genre: state.app.selectedGenre,
    app: state.app,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    gameActions: bindActionCreators(GameActions, dispatch),
    authActions: bindActionCreators(AuthActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Incorrect);



