let initialState = {
  fbToken: null,
  fbExpires: 0,
  flToken: localStorage['fl_token'] || null,
  flExpires: localStorage['fl_expires'] || 0,
  flExpirationDate: Number(localStorage['fl_expirationDate']) || 0
};

export default (state = initialState, action) => {
  const switchObj = {
    AUTH_SET_FB_CREDENTIALS: () => {
      return Object.assign({},
        state,
        {
          fbToken: action.token,
          fbExpires: action.expires
        }
      );
    },
    AUTH_SET_FL_CREDENTIALS: () => {
      return Object.assign({},
        state,
        {
          flToken: action.token,
          flExpires: action.expires,
          flExpirationDate: action.flExpirationDate
        }
      );
    },
    DEFAULT: ()=> state
  };
  return (switchObj.hasOwnProperty(action.type) && switchObj[action.type] || switchObj.DEFAULT)();
};



