import React, { Component } from 'react';

// require('./_index.scss');

const Leaderboard = ({ friends, genre }) => {
  return (
    <div className="Leaderboard wrapper-container">
      <div className="leaderboard-fix">
        <h1>{genre}</h1>
        <ul>
        {
          friends.map((item, index) => {
            return (
              <li key={index}>
                <img src={item.avatar}/>
                <span className="user-position">{index+1}</span>
                <span className="user-name">{item.name}</span>
                <span className="user-points">{item.points}</span>
              </li>
            );
          })
        }
        </ul>
        <a href="./#/" className="play-again-cta">Just let me play again!</a>
      </div>
    </div>
  );
};

export default Leaderboard;



