import React, { Component } from 'react';

class Player extends Component {

  constructor(props) {
    super(props);
    this.currentTime = 30;
    this.lastUpdate = 0;
    this.clearTimer;
  }

  componentDidMount() {
    this.setState({
      audio: this.refs.audioTrivia
    });
    this.props.methods(this.refs.audioTrivia, this._reset.bind(this));
    this.refs.audioTrivia.ontimeupdate = this._timeupdate.bind(this);
    this.refs.audioTrivia.onplay = this._timer.bind(this);
  }

  render() {
    return (
      <audio ref="audioTrivia" src={this.props.src}></audio>
    );
  }

  _timeupdate() {
    this.lastUpdate = this.state.audio.currentTime;
  }

  _timer() {
    let oldCheck = 0;
    this.clearTimer = setInterval(() => {
      if (this.lastUpdate !== oldCheck) {
        oldCheck = this.lastUpdate;
        this.currentTime--;
      }
      this.props.timeUpdate(this.currentTime);
      if (this.currentTime < 0) {
        clearInterval(this.clearTimer);
      }
    }, 1000);
  }

  _reset() {
    this.currentTime = 30;
    clearInterval(this.clearTimer);
    this.props.timeUpdate(this.currentTime);
  }
}

export default Player;



